import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { createDream, dreamChecklist, createDiary, dreamResolution } from "../services/apiService";
import logo2 from '../image/logo2.png';
import mikeBtn from '../image/mike.png';
import mikeRecordingBtn from '../image/mike_recording.png';
import '../css/createDream.css';
import annyang from "annyang";


const funFacts = [
    ' REM    . REM  Rapid Eye Movement ,      .',
    '   4-6  ,   .     5  50%, 10  90%  .',
    '         .           .',
    '  :       .      ,       ,       .',
    ' :      ,          .              .',
    '  :       .       , ,       .         .'
    // ...  
];


const CreateDream = () => {
    const navigate = useNavigate();
    const [dreamText, setDreamText] = useState('');
    const [dreamData, setDreamData] = useState(null);
    const [dreamResolutionData, setDreamResolutionData] = useState(null); //     state
    const [loading, setLoading] = useState(false);
    const [isRecording, setIsRecording] = useState(false);
    const [checklistData, setChecklistData] = useState(null); //    state
    const [isChecklistLoading, setIsChecklistLoading] = useState(false); //     state
    const [funFact, setFunFact] = useState('');
    const endOfPageRef = useRef(null);



    const handleButtonClick = async () => {
        navigate("/createDream");
        setLoading(true);
        const result = await createDream(dreamText);
        setDreamData(result);
        const resolutionResult = await dreamResolution(dreamText); //   API 
        setDreamResolutionData(resolutionResult); //  
        setLoading(false);

        // checklist "checklist"  .
        setChecklistData({ today_checklist: "checklist" });
    };

    useEffect(() => {
        //    createDiary .
        if (dreamData && dreamResolutionData && checklistData) {
            handleGoToGallery();
        }
    }, [dreamData, dreamResolutionData, checklistData]);

    const handleGoToGallery = async () => {
        const dreamDataToSave = {
            dream_name: dreamData.dream_name,
            dream: dreamData.dream,
            image_url: dreamData.image_url,
            resolution: dreamResolutionData.dream_resolution,
            checklist: checklistData.today_checklist,
            is_public: true
        };
        console.log('dreamDataToSave:', dreamDataToSave);
        const response = await createDiary(dreamDataToSave);
        if (response && response.id) {
            // DiaryRead   id .
            navigate(`/diary/${response.id}`, { state: { id: response.id } });
        }
    };


    const handleInputChange = (event) => {
        setDreamText(event.target.value.slice(0, 200)); //      
    };

    const handleVoiceRecording = () => {
        if (!isRecording) {
            if (annyang) {
                //   
                annyang.setLanguage('ko');

                //   
                var commands = {
                    "*text": function (text) {
                        console.log("  :", text);
                        setDreamText(dreamText + " " + text);
                    },
                };

                //  
                annyang.addCommands(commands);

                //   
                annyang.start();
            }
        } else {
            //   
            if (annyang) {
                annyang.abort();
            }
        }

        setIsRecording(!isRecording);
    };
    useEffect(() => {
        //     
        setFunFact(funFacts[Math.floor(Math.random() * funFacts.length)]);

        // 6   
        const interval = setInterval(() => {
            setFunFact(funFacts[Math.floor(Math.random() * funFacts.length)]);
        }, 8000);

        //  unmount interval clear
        return () => clearInterval(interval);
    }, []);




    return (
        <div className="createDream">
            <img className="logo" src={logo2} alt="logo" />

            {loading ? ( //   
                <div className="loading-effect">
                    <p className="dream_load_text">  <span className="loading-dots">...</span></p>
                    <p className="fun-fact">{funFact}</p>
                </div>
            ) : (
                dreamData ? ( //   
                    <div className="dream-result">
                        {/*<h2 className="dream-title">{dreamData.dream_name}</h2>*/}
                        {/*<img src={dreamData.image_url} alt="Dream" className="dream-image" />*/}
                        {/*<p className="dream-description">{dreamData.dream}</p>*/}
                        {/*{dreamResolutionData && <p className="dream-resolution">{dreamResolutionData.dream_resolution}</p>}*/}
                    </div>
                ) : ( //  
                    <>
                        <p className="text"> </p>
                        <div className="container">
                            <div className="text_field">
                                 <textarea
                                    type="text"
                                    className="input-field"
                                    value={dreamText}
                                    onChange={handleInputChange}
                                    minLength={10}
                                    maxLength={200} //   300 
                                >
                                </textarea>
                                <p className="textNum">{dreamText.length}/200</p> {/*     */}
                            </div>
                            <p className="voice">  .</p>
                            <p className="voice">     .</p>
                            <p className="voice">    !!</p>
                            <img
                                src={isRecording ? mikeRecordingBtn : mikeBtn}
                                alt="record"
                                className="mike-btn"
                                onClick={handleVoiceRecording}
                            />
                        </div>
                        <button onClick={handleButtonClick} className="draw-btn"> </button>
                    </>
                )
            )}
            {/*{dreamResolutionData && (*/}
            {/*    <div ref={endOfPageRef}> /!*    . *!/*/}
            {/*        <button onClick={handleGoToGallery} className="gallery-btn">  </button>*/}
            {/*    </div>*/}
            {/*)}*/}
        </div>
    );
};

export default CreateDream;
